<template>
  <div class="box-card">
    <LayoutBox one="退款管理">
      <h4>退款管理</h4>
      <div class="form_group">
        <el-form :inline="true" :model="form">
          <el-form-item label="申请时间">
            <el-date-picker v-model="form.startTime" type="date" placeholder="选择日期" size="mini"></el-date-picker> 至 <el-date-picker v-model="form.endTime" type="date" placeholder="选择日期" size="mini"></el-date-picker>
          </el-form-item>
          <el-form-item class="reason" label="原因">
            <el-input v-model="form.reason" size="mini"></el-input>
          </el-form-item>
        </el-form>
        <el-form class="refund" :model="form1" :inline="true">
          <el-form-item label="退款类型">
            <!-- 退款类型(0-仅退款1-退货退款） -->
            <el-select v-model="form1.refundType" placeholder="请选择" size="mini">
              <el-option label="仅退款" value="0"></el-option>
              <el-option label="退货退款" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="退款状态">
            <!-- 退款状态（0-退款失败1-退款成功2-退款中）默认退款中 -->
            <el-select v-model="form1.refundStatus" placeholder="请选择" size="mini">
              <el-option label="退款失败" value="0"></el-option>
              <el-option label="退款成功" value="1"></el-option>
              <el-option label="退款中" value="2"></el-option>
            </el-select>
          </el-form-item>
          <!-- 退货方式(0-上门取件1-自行邮寄) -->
          <el-form-item label="退款方式">
            <el-select v-model="form1.refundStyle" placeholder="请选择" size="mini">
              <el-option label="上门取件" value="0"></el-option>
              <el-option label="自行邮寄" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <!-- 后端没有字段<el-form class="refund" :inline="true">
          <el-form-item label="发货状态">
            <el-select v-model="sendStatus" placeholder="请选择" size="mini">
              <el-option label="1" value="AAA"></el-option>
              <el-option label="2" value="BBB"></el-option>
              <el-option label="3" value="CCC"></el-option>
            </el-select>
          </el-form-item>
        </el-form> -->
        <div class="btn_groud">
          <el-select v-model="searchType" placeholder="全部" class="select" size="mini">
            <el-option label="运单号" value="0"></el-option>
            <el-option label="买家昵称" value="1"></el-option>
            <el-option label="商品名称" value="prodName"></el-option>
            <el-option label="退款编号" value="refundCode"></el-option>
            <el-option label="订单编号" value="cover"></el-option>
          </el-select>
          <el-input v-model="searchInput" class="input" size="mini"></el-input>
          <el-button type="success" size="mini" @click="onSearch">查询</el-button>
          <el-button size="mini" @click="onReset">重置</el-button>
        </div>
      </div>

      <el-table :data="returnStock" height="410" border style="width: 100%" size="mini">
        <el-table-column prop="refundCode" label="退款编号" width="180" align="center"></el-table-column>
        <el-table-column label="订单号/商品" align="center" width="180" show-overflow-tooltip>
          <template slot-scope="scope">
            <div class="goods_box">
              <img :src="scope.row.cover" min-width="70" height="70">
              <div>
                <span class="goods_name">{{ scope.row.prodName }}</span>
                <span>订单号：{{ scope.row.orderCode }}</span>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="price" label="交易金额" align="center"></el-table-column>
        <el-table-column prop="refundPrice" label="退款金额" align="center"></el-table-column>
        <el-table-column prop="createTime" label="退款时间" width="180" align="center"></el-table-column>
        <el-table-column prop="refundCause" label="退款原因" width="180" align="center"></el-table-column>
        <!-- <el-table-column prop="logisticsType" label="物流状态" align="center"></el-table-column> -->
        <el-table-column prop="date" label="退款类型" width="180" align="center">
          <template slot-scope="scope">
            <p>{{ scope.row.serviceType === 0 ? '仅退款' : '退货退款' }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="date" label="退款状态" width="180" align="center">
          <template slot-scope="scope">
            <p v-if="scope.row.refundStatus === 4">退款拒绝</p>
            <p v-else-if="scope.row.refundStatus === 2">退货拒绝</p>
            <p v-else-if="scope.row.orderStatus === 4">退款中</p>
            <p v-else-if="scope.row.orderStatus === 9">订单关闭</p>
            <router-link v-if="btnPermissions.indexOf('退款详情按钮') !== -1" style="color: #2ea2e4" :to="{path: '/refundList/refundContent', query:{ id: scope.row.id }}">详情</router-link>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="total"></el-pagination>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import { MIXINS_BTNPERMISSIONS_DATA } from '@/mixins/data.js'

export default {
  name: 'RefundList',

  components: {
    LayoutBox
  },

  data () {
    return {
      searchType: '', // 搜索框类型
      searchInput: '', // 搜索框输入
      pageNo: 1, // 分页当前页
      total: 0, // 分页总条数
      returnStock: [], // 退款列表
      form: {}, // 第一行的搜索表单
      form1: {}, // 第二行的搜索表单
    }
  },

  mixins: [
    MIXINS_BTNPERMISSIONS_DATA
  ],

  methods: {
    // 点击查看
    handleClick(index, rows) {
      console.log(index, rows)
    },
    // 点击查询
    onSearch () {
      this._getReturnStockList()
    },
    // 重置
    onReset () {
      this.searchType = ''
      this.searchInput = ''
      this.form = {}
      this.form1 = {}
    },

    // api 接口
    // 退货列表
    _getReturnStockList () {
      let data = {
        pageNo: this.pageNo,
      }
      // 模糊查询
      data[this.searchType] = this.searchInput
      Object.assign(data, this.form)
      Object.assign(data, this.form1)
      this.$http.post('/woho/order/returnStock/query', data)
        .then((res) => {
          if (res.data.success) {
            this.total = res.data.data.total
            this.returnStock = res.data.data.list
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
  },

  created () {
    this._getReturnStockList()
  },
}
</script>

<style lang="scss" scoped>
  $themColor: #5bd9be;

  .box-card {
    min-width: 1100px;
    .form_group{
      margin-bottom: 28px;
      .reason{
        margin-left: 30px;
      }
      .refund{
        margin-top: -10px;
      }
      .btn_groud{
        display: flex;
        .select{
          width: 150px;
        }
        .input{
          width: 259px;
          margin: auto 15px;
        }
      }
    }
    .goods_box {
      display: flex;
      align-items: center;
      .goods_name{
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
        text-align: left;
        margin: 5px;
      }
    }
  }
</style>
